@-webkit-keyframes blink {
  to {color: transparent}
}

@keyframes blink {
  to {color: transparent}
}

.word {
  font-size: 1.9vh;
}

.WordBank {
  width: 20vw;
  height: 90vh;
  position: absolute;
  left: 5vw;
  background: -webkit-gradient(linear, right top, left top, from(#e9ece5), to(#e3e3e3));
  background: -webkit-linear-gradient(right, #e9ece5, #e3e3e3);
  background: -o-linear-gradient(right, #e9ece5, #e3e3e3);
  background: linear-gradient(to left, #e9ece5, #e3e3e3);
  top:10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  border-right: 5px solid #3b3a36;
}

.WordBank > h3 {
  font-size: 30px;
  color: #3b3a36;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
.WordBank > h1 {
  position: absolute;
  top: 2em;
  font-size: 1.2em;
  color: #3b3a36;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 0;

}
.WordBank:hover > h3 {
  opacity: 0;
}
.WordBank:hover > h1 {
  -webkit-transition-delay: .5s;
       -o-transition-delay: .5s;
          transition-delay: .5s;
  opacity: 1;
  -webkit-animation: blink 1s linear infinite alternate;
          animation: blink 1s linear infinite alternate;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.WordBank > ul {
  position: absolute;
  z-index: 1;
  overflow-y: auto;
  overflow-x: hidden;
  width: 85%;
  top: 10vh;
  height: 60vh;
  padding-left: 3vh;
  left: 3vh;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-box-shadow: -3px 0px 50px -16px black;
          box-shadow: -3px 0px 50px -16px black;
  padding-top: 10px;
  border-radius: 10px;
}

.WordBank > div > p {
  color: black;
  font-size: 2vh;
  margin-right: 10%;
  cursor: pointer;
}

.activeBtn {
  background: #EC644B;
  color: white;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.activeBtn:hover {
  opacity: 1;
  -webkit-box-shadow: -3px 0px 70px -16px black;
          box-shadow: -3px 0px 70px -16px black;
}

.inactiveBtn {
  cursor: default;
  background: darkgrey;
  color: grey;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;

}
.Btn {
  border-radius: 10px;
  width: 60%;
  height: 7vh;
  font-size: 1.3em;
  font-weight: 900;
  letter-spacing: 1px;
  border: none;
  position: absolute;
  bottom: 50px;
  z-index: 2;
  opacity: .7;
}



