.AddWordBar {
  position: absolute;
  right: 0;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack:end;
      justify-content:flex-end;
  height: 10vh;
  width: 100vw;
  background: #3b3a36;
  z-index: 3;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.AddWordBar>input {
  background: -webkit-gradient(linear, left top, right top, from(#e9ece5), to(lightgrey));
  background: -webkit-linear-gradient(left, #e9ece5, lightgrey);
  background: -o-linear-gradient(left, #e9ece5, lightgrey);
  background: linear-gradient(to right, #e9ece5, lightgrey);
  padding-left: 2%;
  width: 50%;
  height: 60%;
  border-radius: 50px;
  color: black;
  font-size: 3.5vh;
  font-weight: bold;
  border:3px solid #3b3a36;
}

.AddWordBar>input::-webkit-input-placeholder {
  color: darkgrey;
}

.AddWordBar>input:-ms-input-placeholder {
  color: darkgrey;
}

.AddWordBar>input::placeholder {
  color: darkgrey;
}
.AddWordBar>div {
  color: white;
  position: absolute;
  left: 5vw;
  font-size: 4em;
}
.AddWordBar>button {
  border-radius: 50%;
  margin-left: .5vw;
  margin-right: 2vw;
  width: 5vh;
  height: 5vh;
  border: none;
  background: #EC644B;
  position: relative;
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s;

}
.AddWordBar>button:hover {
  opacity: .5;
}
.AddWordBar>button>.glyphicon {
  position: absolute;
  top: 30%;
  left: 30%;
  color: white;
  font-size: 2vh;
  font-weight: 100;
}



