@import url('https://fonts.googleapis.com/css?family=Varela+Round');
@-webkit-keyframes blinker {
  to {opacity: 1}
}
@keyframes blinker {
  to {opacity: 1}
}
html, body, #root {
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'Varela Round', sans-serif;
}

.Warning {
  position: absolute;
  font-size: 2em;
  letter-spacing: 2px;
  font-weight: 900;
  background: -webkit-radial-gradient(center, white, #3b3a36);
  background: -o-radial-gradient(center, white, #3b3a36);
  background: radial-gradient(to center, white, #3b3a36);
  color: #3b3a36;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.ContentWrapper {
  position: absolute;
  z-index: 2;
  top: 10vh;
  left: 24.9vw;
  bottom: 0;
  right: 0;
  background: -webkit-gradient(linear, left top, right top, from(#e9ece5), to(lightgrey));
  background: -webkit-linear-gradient(left, #e9ece5, lightgrey);
  background: -o-linear-gradient(left, #e9ece5, lightgrey);
  background: linear-gradient(to right, #e9ece5, lightgrey);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

}



