@import url("//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css");

.glyphicon {
  font-size: 2.5em;
  color: #edf5e1;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
.Container {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background: #e3e3e3;
  height: 100%;
  width: 5vw;
}
.SideBar {
  -webkit-box-shadow: -6px 0px 35px 10px rgba(0,0,0,.5);
          box-shadow: -6px 0px 35px 10px rgba(0,0,0,.5);
  position: absolute;
  top: 16vh;
  height: 55%;
  width: 100%;
  z-index: 100;
}
.Button {
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 25%;
  background: #3b3a36;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  border-top: 1px solid rgba(0,0,0,.2);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Button:hover .tooltip {
  opacity:1;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Button:hover .glyphicon {
  opacity:.1;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.glyphicon > img {
  height: 80%;
  width: 80%;
  color: white;
}

.Button:active {
  background: rgba(0,0,0,.5);
  -webkit-transition: .001s;
  -o-transition: .001s;
  transition: .001s;
}

.ToggleOn {
  background: #EC644B;
}

.tooltip {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  opacity: 0;
  font-size: 1em;
  height: 100%;
  width: 100%;
  color: #edf5e1;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
}

.tooltip:hover{
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
}

.ModeText {
  position: absolute;
  opacity: 1;
  font-size: 2em;
  color: #edf5e1;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
.Save {
  border: none;
}
@media screen and (max-width: 2000px) {
   .tooltip, .ModeText {
      font-size: 1.4em;
   }

}



